export default {
	data() {
		return {
			goodsData:{}
			// //轮播主图数据
			// swiperList: [
			// 	{ id: 1, img: '../../static/images/lunxia.png' },
			// 	{ id: 2, img: '../../static/images/lunxia.png' },
			// 	{ id: 3, img: '../../static/images/lunxia.png' },
			// 	{ id: 4, img: '../../static/images/lunxia.png' }
			// ],
			// currentSwiper: 0,
			// anchorlist:[],//导航条锚点
			// selectAnchor:0,//选中锚点
		}
	},
	onLoad: function (e) {
	  var ids = e.id;
		uni.showToast({
			title: "编号" + ids,
			icon: "none"
		});
	  //获得当前对象
	  var that = this
		uni.request({
			url: 'http://localhost:8080/commodity/getById?id='+ids,
			method: 'POST',
			success: res => {
				this.goodsData=res.data;
				this.comment=res.data;
				console.log(res.data)
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		home:function(){
			uni.navigateTo({
				url:'../../home/home/home'
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		//服务弹窗
		showService() {
			console.log('show');
			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		// 首页
		// home(){
		// 	uni.navigateTo({
		// 		url:'../../home/home/home'
		// 	})
		// },
		// 客服
		share(){
			uni.navigateTo({
				url:""
			})
		},
		//购物车
		keep(){
			uni.navigateTo({
				url:""
			})
		},
		// 加入购物车
		joinCart(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					uni.showToast({title: "已加入购物车"});
				});
			}
			uni.showToast({title: "已加入购物车"});
		},
		//点击加入购物车
		handleCartAdd() {
			console.log(this.goodsData);
			// 1 获取缓存中的购物车 数组
			let cart = uni.getStorageSync("cart") || [];
			// 2 判断 商品对象是否存在于购物车数组中
			let index = cart.findIndex(v => v.id === this.goodsData.id);
			if (index === -1) {
				//3  不存在 第一次添加
				this.GoodsInfonew.bigpicturepath = this.goodsData.bigpicturepath;
				this.GoodsInfonew.activityprice = this.goodsData.activityprice;
				this.GoodsInfonew.price = this.goodsData.price;
				this.GoodsInfonew.goodsName = this.goodsData.goodsName;
				this.GoodsInfonew.number = 1;
				this.GoodsInfonew.id = this.goodsData.id;
				cart.push(this.GoodsInfonew);
			} else {
				// 4 已经存在购物车数据 执行 num++
				cart[index].number++;
			}
			// 5 把购物车重新添加回缓存中
			uni.setStorageSync("cart", cart);
			// 6 弹窗提示
			uni.showToast({
				title: '加入成功',
				icon: 'success',
				// true 防止用户 手抖 疯狂点击按钮 
				mask: true
			});
		},
		//立即购买
		buy(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		},
		chat() {
			uni.reLaunch({
				url:'/pages/home/chat/chat'
			})
		}
	}
}
